import React from "react";
import {
  format,
  getYear,
  getDate,
  getHours,
  subMinutes,
  addMinutes,
  getMilliseconds,
} from "date-fns";
import { timer } from "rxjs";

import "./App.scss";

const MAX_MINUTES = 360;
const NEGATIVE_THRESHOLD = 280;

function getJST() {
  const current = Date.now();
  const currentDay = getDate(current) + 1;
  const currentYear = getYear(current) + 1;
  const currentHour = getHours(current) + 1;

  const multiple = currentDay * currentHour * currentYear;
  const minutes = multiple % MAX_MINUTES;
  if (minutes < NEGATIVE_THRESHOLD) {
    return subMinutes(current, minutes);
  }
  return addMinutes(current, minutes);
}

const delay = 1000 - (getMilliseconds(Date.now()) % 1000);
const source$ = timer(delay, 1000);

function App() {
  const [currentJST, setJST] = React.useState<Date | null>(null);

  React.useEffect(() => {
    const sub = source$.subscribe(() => {
      setJST(getJST());
    });

    return () => {
      sub.unsubscribe();
    };
  }, []);

  return (
    <div className="root">
      <div className="top" />
      <div className="main">
        <div className="content">Current Joshua Standard Time (JST)</div>
        <div className="content">
          {currentJST && format(currentJST, "HH:mm:ss")}
        </div>
        <div className="content">
          {currentJST && format(currentJST, "dd/MM/yyyy")}
        </div>
      </div>
      <div className="bottom"></div>
    </div>
  );
}

export default App;
